@import "../../main.scss";

.card {
    @include card-d;

    @media screen and (max-width: $breakpoint) {
        @include card-m;
    }

    &__img {
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__overlay {
        @include card-overlay;
    }

    &__title {
        @include card-title-d;
    }
}